<template>
  <div >
    <div class="container">
      <div class='item'>
            <view class="label"><span style="color: #f00">*</span> 输入小区名：</view>
            <input type="text" v-model='name' placeholder="请输入小区名" class='input'>
        </div>
        <div class='item'>
            <view class="label"><span style="color: #f00">*</span> 输入价格：</view>
            <input type="digit" v-model='min_price' placeholder="下限/元" class='input'>
            <input type="digit" v-model='max_price' placeholder="上限/元" class='input'>
        </div>
        <div class='item'>
            <view class="label"><span style="color: #f00">*</span> 输入年代：</view>
            <input type="number" v-model='time' placeholder="请输入年代" class='input'>
        </div>
        <div class='item'>
            <view class="label"><span style="color: #f00">*</span> 输入地址：</view>
            <input type="text" v-model='address' placeholder="请输入地址" class='input'>
        </div>
        <button  @click="onAdd">保存</button>
    </div>
    <van-notify id="van-notify" />  
  </div>

</template>

<script>
import Notify from '@/../static/vant//notify/notify';
export default {
  data() {
    return {
      name: "",
      min_price: "",
      max_price: "",
      address: "",
      time: ""
    };
  },

  components: {},

  methods: {
    onAdd: async function() {
      if (
        !this.name ||
        !this.min_price ||
        !this.max_price ||
        !this.time ||
        !this.address
      )
        return Notify("请输入必选项");
      const db = wx.cloud.database();
      const { data } = await db
        .collection("hourse")
        .where({ name: this.name })
        .get();
      const len = data.length ? data[0]._id : "";
      const obj = {
        name: this.name,
        min_price: this.min_price,
        max_price: this.max_price,
        time: this.time,
        address: this.address
      };
      if (len) {
        db
          .collection("hourse")
          .doc(len)
          .set({
            data: obj,
            success: function(res) {
              Notify({
                text: '更新成功！',
                duration: 1000,
                selector: '#van-notify',
                backgroundColor: '#38f'
              })
            }
          });
      } else {
        db.collection("hourse").add({
          data: obj,
          success: function(res) {
            Notify({
              text: '更新成功！',
              duration: 1000,
              selector: '#van-notify',
              backgroundColor: '#38f'
            })
          }
        });
      }
      this.name = "";
      this.min_price = "";
      this.max_price = "";
      this.time = "";
      this.address = "";
    }
  },

  created() {
    // this.getUserInfo()
    wx.setNavigationBarTitle({
      title: '录入房源'
    })
  }
};
</script>

<style scoped>
.container {
  box-sizing: border-box;
  padding: 0 20px;
  padding-top: 15px;
}
.item {
  display: flex;
  /* flex-direction: column; */
  align-items: center;
  height: 38px;
  margin-bottom: 15px;
}
.item .label {
  width: 120px;
  font-size: 16px;
}
.item .input {
  flex: 1;
  font-size: 16px;
  border-bottom: 1px solid #ddd;
}
button {
  background: #22b37c;
  color: #fff;
}
</style>



